<template>
	<div class="main">
		<mt-navbar id="navBar"  v-model="selected">
	    	<mt-tab-item id="a"></mt-tab-item>
	    	<!--  -->
	    	<mt-tab-item id="b"></mt-tab-item>
	    	<mt-tab-item id="c"></mt-tab-item>
	    	
	    </mt-navbar>
		<mt-tab-container v-model="selected">
		  <mt-tab-container-item id="a">
		  	<!-- -->
		    <div class="lite-divider">
	            <input placeholder="" class="hidden-input" style="text-align:left;" v-model="stmtId">
	            <div class="scan-icon" @click="show"></div>
	        </div>
			<mt-button class="subBtn" size="large" @click.native="submit" ></mt-button>
			<mt-loadmore 
			  	:bottomMethod="quickloadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "quickisLastPage"
			  	>
			  		<Bill :bill-info="quickBillInfo" @itemClick="itemClick"></Bill>
			</mt-loadmore>
			<div style="widtt:100%;height:0.92rem;"></div>
			<div class="btn-fixed">
	    		<div class="fl select-btn" :class="{allSelected:quickAllselect}" @click="allSelect(quickBillInfo,'quickAllselect')">&nbsp;</div>
	    		<div class="pay" @click="pay('quickBillInfo','quickAllPrice','quickAllselect')">
	    			
	    			<span>
	    				{{quickAllPrice}}
	    			</span>
	    		</div>
    		</div> 	  
			<!--  -->
		  </mt-tab-container-item>
		  <mt-tab-container-item id="b">
			<!--  -->
		    	<mt-loadmore 
			  	:bottomMethod="loadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "bisLastPage"
			  	>
			  		<Bill :bill-info="billInfo" @itemClick="itemClick"></Bill>
			 	</mt-loadmore>
			 	<div style="widtt:100%;height:0.92rem;"></div>
		    	<div class="btn-fixed">
		    		<div class="fl select-btn" :class="{allSelected:bAllSelect }" @click="allSelect(billInfo,'bAllSelect')">&nbsp;</div>
		    		<div class="pay" @click="pay('billInfo','allPrice','bAllSelect')">
		    			
		    			<span>
		    				{{allPrice}}
		    			</span>
		    		</div>
        		</div> 
		    <!--  -->
		  </mt-tab-container-item>
		  <mt-tab-container-item id="c">
			<!--  -->
			  <mt-loadmore 
			  	:bottomMethod="carLoadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "carisLastPage"
			  >
			  	<Bill :bill-info="carBillInfo" @itemClick="itemClick"></Bill>
			  </mt-loadmore>
			  <div style="widtt:100%;height:0.92rem;"></div>
			  	<div class="btn-fixed">
		    		<div class="fl select-btn" :class="{allSelected:carAllselect }" @click="allSelect(carBillInfo,'carAllselect')">&nbsp;</div>
		    		<div class="pay" @click="pay('carBillInfo','carAllPrice','carAllselect')">
		    			
		    			<span>
		    				{{carAllPrice}}
		    			</span>
		    		</div>
        		</div>
			<!--  -->
		  </mt-tab-container-item>
		  <!--  -->
		  <mt-tab-container-item id="d">
		  	<div class="query-data">
		  		<div class="input-row">
			  		 
			  		<select class="virtual-input" v-model="query.sect">
			  			<!-- <option value="" disabled></option> -->
			  			<option v-for="item in sectList" :value="item.id" >{{item.name}}</option>	
			  			
			  		</select>
			  	</div>
			  	<div class="input-row">
			  		
			  		<select class="virtual-input" v-model="query.build">
			  			<!-- <option value="" disabled></option> -->
			  			<option v-for="item in buildList" :value="item.id" >{{item.name}}</option>	
			  		</select>
			  	</div>
			  	<div class="input-row">
			  		
			  		<select class="virtual-input" v-model="query.unit">
			  			<!-- <option value="" disabled></option> -->
			  			<option v-for="item in unitList" :value="item.id" >{{item.name}}</option>	
			  		</select>
			  	</div>
			  	<div class="input-row">
			  		
			  		<select class="virtual-input" v-model="query.house">
			  			<!-- <option value="" disabled></option> -->
			  			<option v-for="item in houseList" :value="item.id">{{item.name}}</option>	
			  		</select>
			  	</div>	
		  	</div>
		  	<mt-loadmore 
			  	:bottomMethod="queryLoadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "queryisLastPage"
			>
			  	<Bill :bill-info="queryBillInfo" @itemClick="itemClick"></Bill>
			</mt-loadmore>
			<div style="widtt:100%;height:0.92rem;"></div>
			<div class="btn-fixed">
	    		<div class="fl select-btn" :class="{allSelected:queryAllselect }" @click="allSelect(queryBillInfo,'queryAllselect')">&nbsp;</div>
	    		<div class="pay" @click="pay('queryBillInfo','queryAllPrice','queryAllselect')">
	    			
	    			<span>
	    				{{queryAllPrice}}
	    			</span>
	    		</div>
       		</div>
		  	
		  </mt-tab-container-item>
		</mt-tab-container>
	</div>
</template>
<script>
	let vm;
	import wx from 'weixin-js-sdk';
	import Bill from '../../components/bill.vue';
	export default {
	  components:{Bill},
	  watch:{
	  	'query.sect':{//
	  		deep : true,
		  	handler:(val,oldval) => {
		  		// 
		  		vm.getCellMng(val, "", "", '03');// 
		  		vm.getCellMng(val, "0", "", '02');//
				vm.getCellMng(val, "0", "0", '01');//
		  	}	
	  	},
	  	'query.build':{//
	  		deep : true,
		  	handler:(val,oldval) => {
		  		// 
		  		vm.getCellMng(vm.query.sect, val, "", '02');//
    			vm.getCellMng(vm.query.sect, val, "0", '01');//
		  	}	
	  	},
	  	'query.unit':{//
	  		deep : true,
		  	handler:(val,oldval) => {
		  		// 
		  		if (vm.query.build == '') {
    				vm.query.build = '0';
				}
    			vm.getCellMng(vm.query.sect, vm.query.build, val, '01');
		  	}	
	  	},
	  	'query.house':{//
	  		deep : true,
		  	handler:(val,oldval) => {
		  		//
		  		vm.queryBillInfo= [];//
    			vm.queryBillPage = 1;//
    			vm.queryisLastPage=false;//
    			//
    			vm.queryBillList();

		  	}	
	  	},
	  },
	  computed:{
	  	//
	  	allPrice : function(){
	  		let ap = 0;
	  		for(let i in this.billInfo){
	  			if(this.billInfo[i].selected == true){
	  				ap+=Number(this.billInfo[i].fee_price)
	  			}
	  		}
	  		return parseFloat(ap).toFixed(2)
	  	},
	  	//
	  	carAllPrice : function(){
	  		let ap = 0;
	  		for(let i in this.carBillInfo){
	  			if(this.carBillInfo[i].selected == true){
	  				ap+=Number(this.carBillInfo[i].fee_price)
	  			}
	  		}
	  		return parseFloat(ap).toFixed(2)
	    },
	    //
	    quickAllPrice : function(){
	    	let ap = 0;
	  		for(let i in this.quickBillInfo){
	  			if(this.quickBillInfo[i].selected == true){
	  				ap+=Number(this.quickBillInfo[i].fee_price)
	  			}
	  		}
	  		return parseFloat(ap).toFixed(2)
	    },
	    //
	    queryAllPrice :function(){
	    	let ap = 0;
	  		for(let i in this.queryBillInfo){
	  			if(this.queryBillInfo[i].selected == true){
	  				ap+=Number(this.queryBillInfo[i].fee_price)
	  			}
	  		}
	  		return parseFloat(ap).toFixed(2)
	    }
	  },
	  data(){
	  	return {
	  		sectList:[],//
	  		buildList:[],//
	  		unitList:[],//
	  		houseList:[],//
	  		query:{//
	  			sect:'',//id
	  			build:'',//id
	  			unit:'',//id
	  			house:'',//id
	  		},

	  		stmtId:'',// 
	  		url : '/billList',
	  		params : {
	  			startDate:'',
	  			endDate:'',
	  			payStatus: '02', //
	  			currentPage : 1, //
	  			totalCount : 0, //
	  		},
	  		bisLastPage:false,//
	  		carisLastPage:false,//
	  		quickisLastPage:false,//
	  		queryisLastPage:false,//
	  		bAllSelect:false,//
	  		carAllselect:false,//
	  		quickAllselect:false,//
	  		queryAllselect:false,//
	  		billInfo:[],//
	  		carBillInfo:[],//
	  		queryBillInfo : [],//
	  		quickBillInfo:[],//
	  		selected:'a', // 
	  		billPage :1, // 
	  		carBillPage :1, //
	  		queryBillPage : 1,//
	  		quickBillpage:1,
	  		reduceMode :'',//
	  	}
	  },
	  created(){
	  	vm = this;
	  },
	  mounted(){
	  	//
	  	let url = location.href.split('#')[0]
	  	vm.receiveData.wxconfig(vm,wx,['scanQRCode'],url);
	  	
	  	//   
	  	vm.receiveData.getData(vm, vm.url, 'data',function(){
	  		vm.billInfo = vm.data.result.bill_info;//
	  		vm.reduceMode = vm.data.result.reduce_mode;//
	  		vm.carBillInfo = vm.data.result.car_bill_info;//
	  	},vm.params) ;
	  	// 
	  	vm.receiveData.getData(vm,'getSect','sectList',function(){
	  		vm.sectList = vm.sectList.result
	  	})
	  },
	  methods:{
	  	//    
	  	// 1 id 2 id 3 id 4 030201
	  	getCellMng(sect_id,build_id,unit_id,data_type){
			let url = 'getcellbyid';
			let params = {
				sect_id, 
				build_id,
				unit_id,
				data_type 
			};
			vm.receiveData.getData(vm,url,'queryInfo',function(){
				//console.log(vm.queryInfo)
				let InfoList = vm.queryInfo.result;
				vm.queryBillInfo  = [];//
				if ("03"==data_type) {
				 	vm.buildList = InfoList.build_info;
					vm.unitList = [];
					vm.houseList = [];
				}else if("02"==data_type){
					vm.unitList= InfoList.unit_info;
					vm.houseList = [];
				}else if("01"==data_type){
					vm.houseList = InfoList.house_info;
				}
			},params)
		},
		// 
		queryBillList(){
			let url = 'billList';
			vm.params.house_id = vm.query.house;
			vm.params.currentPage = 1;
			vm.receiveData.getData(vm,url,'queryBillInfo',function(){
				vm.queryBillInfo = vm.queryBillInfo.result.bill_info;
			},vm.params)
		},

	  	submit(){
	  		//
	  		if(vm.stmtId == "" || vm.stmtId.length != 18){
	  			alert('');
	  			return 
	  		}
	  		 vm.quickBillpage = 1;
	  		let url = "quickPayBillList/"+vm.stmtId+"/"+vm.quickBillpage+"/"+vm.params.totalCount;
	  		vm.receiveData.getData(
	  			vm,
	  			url,
	  			'quickData',
	  			function(){
	  				if( vm.quickData.result && vm.quickData.result.bill_info.length>0){
						vm.quickBillInfo = vm.quickData.result.bill_info;
	  				}else{
	  					alert('')
	  				}
	  			}
	  		)

	  	},
	  	queryLoadBottom(){//
	  		let tempArr = null;
	  		//1
	  		vm.queryBillPage += 1;
	  		vm.params.currentPage = vm.queryBillPage;
	  		let url = 'billList';
	  		vm.receiveData.getData(vm,url,'pageData4',function(){
	  			tempArr = vm.pageData4.result.bill_info;
	  			if( tempArr && tempArr.length > 0){
  					vm.queryBillInfo =vm.queryBillInfo.concat(tempArr) //
  					vm.queryAllselect = false;
	  			}else{
  					vm.queryisLastPage = true;
	  			}
			},vm.params)
	  	},
	  	quickloadBottom(){//
	  		//
	  		let tempArr = null;
	  		//1
	  		vm.quickBillpage +=1;
	  		let url = "quickPayBillList/"+vm.stmtId+"/"+vm.quickBillpage+"/"+vm.params.totalCount;
	  		//
	  		vm.receiveData.getData(
	  			vm,
	  			url,
	  			'pageData3',
	  			function(){
	  				tempArr = vm.pageData3.result.bill_info;
	  				if( tempArr && tempArr.length > 0){
	  					vm.quickBillInfo =vm.billInfo.concat(tempArr) //
	  					vm.quickAllselect = false;
	  				}else{
	  					vm.quickisLastPage = true;
	  				}
	  			}
	  		)
	  	},
	  	loadBottom(){// 
	  		//
	  		let tempArr = null;
	  		// 
	  		vm.billPage += 1;
			vm.params.currentPage = vm.billPage;
			//
			vm.receiveData.getData(
				vm,
				vm.url,
				'pageData',
				function(){
	  				tempArr = vm.pageData.result.bill_info;//
	  				if(tempArr && tempArr.length > 0){
	  					
	  					vm.billInfo =vm.billInfo.concat(tempArr) //
	  					vm.bAllSelect = false;
	  				}else{
	  					vm.bisLastPage = true;
	  				}
	  			},
	  			vm.params
	  		) 
	  	},
	  	carLoadBottom(){//
	  		//
	  		let tempArr = null;
	  		// 
	  		vm.carBillPage+=1
			vm.params.currentPage = vm.carBillPage;
			//
			vm.receiveData.getData(
				vm,
				vm.url,
				'pageData2',
				function(){
	  				tempArr = vm.pageData2.result.car_bill_info;//
	  				if(tempArr && tempArr.length > 0){
	  					vm.carBillInfo =vm.carBillInfo.concat(tempArr) //
	  					vm.carAllselect = false;
	  				}else{
	  					vm.carisLastPage = true;
	  				}
	  			},
	  			vm.params
	  		)
	  	},
	  	//
	  	pay(list,allPrice,allselect){//    , string
	  		if( vm[allPrice] < 0.1){
	  			alert('');
	  			return
	  		}
	  		let selectedArr = [];
	  		if(vm[allselect] == true){
	  			//
	  			selectedArr = vm[list];
	  		}else{
	  			//
	  			for( let i in vm[list]){
	  				if(vm[list][i].selected){
	  					selectedArr.push(vm[list][i])
	  				}
	  			}
	  		}
	  		let bills="";
	  		let pay_addr = selectedArr[0].pay_addr;
	  		for(let i in selectedArr){
	  			if(selectedArr.length - 1 == i){
	  				bills+=selectedArr[i].bill_id;
	  			}else{
	  				bills+=selectedArr[i].bill_id+',';	
	  			}
	  			
	  		}
	  		let url = vm.str + "paymentdetail.html?billIds="+bills+
            	"&stmtId="+vm.stmtId+"&payAddr="+escape(pay_addr)+"&totalPrice="+vm[allPrice]+"&reduceMode="+vm.reduceMode;
            	window.location.href = url;
	  	},

	  	//,  stmtId,input
	  	show(){
	  	 	vm.receiveData.scan(vm,wx,'stmtId');
	  	},
	  	// params1: params2:
	  	itemClick:function(index,b){//3 
	  		let len = b.length
	  		if(b[index].selected){//
	  			for(let i=index;i< len;i++){
	  				//
	  				vm.$set(b[i],'selected',false);	
	  			}
	  			//
 	  			vm.bAllSelect = false;
	  		}else{//
	  			for(let j=index;j >= 0;j--){//
	  				vm.$set(b[j],'selected',true);	
	  			}
	  		}
	  	},

	  	// params:
	  	allSelect:function(arr,a){
	  		if(vm[a] ){//
	  			for(let i in arr){
	  				vm.$set(arr[i],'selected',false);
	  			}
	  		}else{//
	  			for(let i in arr){
	  				vm.$set(arr[i],'selected',true);
	  			}
	  		}
	  		//
	  		vm[a] = !vm[a];
	  	}
	}
}
</script>
<style>
	.mint-tab-item-label{
		font-size: 0.3rem;
	}
</style>
<style scoped> 

	/**/
	
	.query-data{
		height: 100%;
		padding: 0.25rem 1.2rem;
		margin-bottom: 0.2rem;
	}
	.virtual-input{
		width:3.4rem;
		border:1px solid #4c4c4c;
		border-radius: 5px;
		padding-left: 0.2rem;
		display: inline-block;
		height: 0.5rem;
	}
	.input-row{
		padding-bottom: 0.3rem;
		font: 0.26rem/0.5rem "";
		color: #a6937c;
		height: 0.5rem;
	}
	.top2{
		position: relative;
		top: -0.2rem;

	}
	/*footbtn start*/
	.blank{
		padding-bottom: 1rem;
	}

	.btn-fixed{
		position: fixed;
		color: #fff;
        left: 0;
        right: 0;
        bottom: 0;
        height: 0.92rem;
        line-height: 0.92rem;
        text-align: center;
	}
	
	.select-btn{
		padding-left: 36px;
        background: url('../../../static/image/icon_unselect_white.png') no-repeat;
        background-color: rgba(0,0,0,0.6);
        background-size: 16px;
        background-position: 15px center;
        height: 0.92rem;
        line-height: 0.92rem;
	}

	.allSelected{
		background: url('../../../static/image/icon_selected_white.png') no-repeat;
		 background-color: rgba(0,0,0,0.6);
        background-size: 16px;
        background-position: 15px center;

	}

	.pay{
		overflow: hidden;
		background: #ff8a00;
		text-align: center;	
	}

	 
	/*footbtn end*/

	.main{
 		margin:0 0.3rem;
 	}

	 .mint-navbar .mint-tab-item{
	 	border-bottom: 1px solid #cdcdcb;
	 }

	 .mint-navbar .mint-tab-item.is-selected{
	 	border-bottom: 1px solid #ff8a00;
	 	margin-bottom: 0;
	 	color:#ff8a00;
	 }

	 .lite-divider{
		border-bottom: 1px solid #d4cfc8;
		-padding-left: 0.15rem;
		overflow: hidden;
		padding: 1px;
		margin-top:35px;
	}
	.hidden-input{
		height: 0.4rem;
		margin-top: 0.4rem;
		width: 4rem;
		-background: black;
		border: none;
		outline: none;
		background-color: transparent;
		font-size: 0.28rem;
		padding: 0 0.15rem 0.15rem;
	}

	.scan-icon{
		float: right;
		display: inline-block;
		height: 0.4rem;
		width: 0.4rem;
		background: url('../../../static/image/icon_scan.png') ;
		background-size: cover;
		margin-top: 10px;
	}
	.subBtn{
		height: 0.88rem;
		line-height: 0.88rem;
		background-color: #ff8a00;
		font-size: 0.3rem;
		color: white;
		width: 6rem;
		margin: 0.6rem auto 0;
	}

</style>


