import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index'//

import My from '@/components/my' // 
import Service from '@/components/service' //
import Market from '@/components/market' //
import Community from '@/components/community' //
//
import Myhouse from '@/views/my/myhouse'
import AddHouse from '@/views/my/add-house'
import BindHouse from '@/views/my/bind-house'

//
import Pay from '@/views/pay/pay'
import PayDetail from '@/views/pay/pay-detail'
import UptonList from '@/views/pay/upton-list'
Vue.use(Router)

const router= new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'Index',
    //   component: Index,
  	 //  children:[
    //   	//{path:'', component: Service },
    //     {path:'', component: My },// 
    //   	{path:'service',component: Service},
    //   	{path:'market',component:Market},
    //   	{path:'community',component:Community},
   	//   ]
    // },

    // {
    //   path:'/',
    //   name:'Myhouse',
    //   component:Myhouse
    // },

    {
      path:'',
      name:'Pay',
      component:Pay
    },
    {
    	path :'/my',
    	name : 'My',
    	component :My,
      meta:{
        title:''
      }
    },
    {
      path:'/myhouse',
      name:'Myhouse',
      component: Myhouse,
      meta:{
        title:''
      }
    },
    {
      path:'/addHouse',
      name:'AddHouse',
      component:AddHouse,
      meta:{
        title:''
      }
    },
    {
      path:'/bindHouse/:number',
      name:'BindHouse',
      component:BindHouse,
      meta:{
        title:''
      }
    },
    {
      path:'/pay',
      name:'Pay',
      component:Pay,
      meta:{
        title:''
      }
    },
    {
      path:'/payDetail/:bills/:stmtId/:totalPrice/:reduceMode',
      name:'payDetail',
      component:PayDetail,
      meta:{
        title:''
      },

    },
    {
      path:'/uptonList',
      name:'uptonList',
      component: UptonList,
      meta:{
        title :''
      }
    }
  ]
});
//
// to  from next() 
router.beforeEach((to, from, next) => {
    //title
    changeTitle(to.meta.title)
    next();
});
//title
function changeTitle(title) {
    title = title ? title : '';
    window.document.title = title;
};

export default router



