<style scoped>
	.pay-detail{
		padding:0.3rem;
	}
	.user-info{
		padding:0.2rem;
		border:1px solid #cecdc9;
		border-radius: 5px;
		height: 1.7rem;
	}
	.user-info .number{
		font:0.28rem/0.8rem "";
		color: #000;
	}
	.user-info .addr{
		font:0.26rem/0.6rem "";
		color: #5b5b5b;
	}
	.fee-list{
		border:1px solid #cecdc9;
		border-bottom: none;
		border-radius: 5px;
	}
	.fee-list dt,.fee-list dd{
		padding: 0 0.2rem;
		border-bottom:1px solid #cecdc9;
		margin: 0;
		line-height:1.2rem;
		height: 1.2rem;
	}
	.fee-list dt p,.fee-list dd p{
		margin:0;
	}
	.fee-list dt .fee-price{
		color: #ff7200;
	}
	.fee-list dd{
		color: #7d7d7d;
	}

	.discount{
		padding: 0;
		background-color: #f6f7f1;
		border:1px solid #cecdc9;
		border-bottom: none;
		list-style: none;

	}
	.discount li{
		padding: 0 0.2rem;
		height: 1rem;
		line-height: 1rem;
		border-bottom:1px solid #cecdc9;
	}
	.discount .can-use{
		display: inline-block;
		width: 1.65rem;
		height: 0.45rem;
		border:1px solid #ff8a05;
		color:#ff8a05;
		font:0.22rem/0.45rem "";
		text-align: center;
	}
	.coupon{
		color: #ff8a05;
	}

	.payCount{
		padding: 0 0.2rem;
		height: 1rem;
		line-height: 1rem;
		background-color: #f6f7f1;
		color: #ff8a05;
		border:1px solid #cecdc9;
	}
	.bigfont{
		font-size: 0.32rem;
		-font-weight: bold;
	}

	/**/
	.upton-list{
		padding: 0 0.3rem;
	}
	.upton-list .can-use{
		font:0.26rem/1rem "";
		height: 1rem;
		border-bottom: 1px solid #878787;
	}
	.uptonDetail{
		position: relative;
		background:url("../../../static/image/bg_courtesy_card.png") no-repeat;
		background-size: 100% 2.15rem;
		margin:0.3rem 0;
		height: 2.15rem;
		padding:0 0.3rem 0 0.4rem;
	}
	.icon{
		background:url('../../../static/image/icon_select.png');
		background-size: cover;
		position:absolute;
		width: 0.4rem;
		height: 0.4rem;
		top: 50%;
		margin-top: -0.2rem;
		margin-left: -0.6rem;
	}
	.iconShow{
		background:url('../../../static/image/icon_selectted.png');
		background-size: cover;
		z-index: 5;
	}
	.detail-left,.detail-right{
		padding-top: 0.35rem;
	}

	.detail-left .upton-name{
		font:0.34rem/0.78rem "";
		color: #ff8a00;
	}
	.detail-left .validity{
		width: 1.7rem;
		height: 0.4rem;
		line-height: 0.4rem;
		text-align: center;
		border:1px solid #ff8a00;
		border-radius: 10%;
		color: #ff8a00;
		margin-bottom: 0.2rem;
	}
	.detail-left .text{
		width: 4.9rem;
	}

	.detail-right .upton-number{
		font:0.466rem/0.8rem "";
		color: #fff;
	}
	
	.detail-right .upton-type{
		font: 0.3rem/0.6rem "";
		color: #fff;
	}
	.btn{
		background-color:#ff8a00; 
		color:#fff;
		width: 6.7rem;
		height: 0.75rem;
		line-height: 0.75rem;
		text-align: center;
		border-radius: 10px;
		position: fixed;
		bottom: 0.5rem;
		-left: 0.4rem;
		margin: 0 auto;
	}

	.pay-btn{
		background-color: #ff8a00;
		position: fixed;
		left: 0;
		bottom: 0;
		bottom: 0;
		z-index: 3;
		width: 100%;
		height: 0.9rem;
		text-align: center;
		color: #fff;
		line-height: 0.9rem;
	}

/**/
.invoice{
	border:1px solid #cecdc9;
	padding-top: 0.5rem;
	margin-top: 0.6rem;
}
.invoice label{
	padding: 0 0.2rem;
}
.form-row{
	padding-bottom: 0.4rem;
}
.mint-cell-wrapper{
	border:none;
}


</style>
<template>
	<div>
		<div  class="pay-detail" v-if="selectUpton">
		<!--  -->
			<div class="user-info">
				<div class="number">&nbsp;{{verNumber}}</div>
				<div class="addr ov">
					<p class="fl">{{addr}}</p>
					<p class="fr">{{area}} m<sup style="font-size:0.2rem">2</sup></p>
				</div>
			</div>
			
			<!--  -->
			<dl v-for="item in feeList" class="fee-list ">
				<dt class="ov">
					<p class="fee-name fl" >{{item.service_fee_name}}</p>
					<p class="fee-price fr" >{{item.totalFee}}</p>
				</dt>
				<dd class="ov" v-for="i in item.fee_detail">
					<p class="detail-date fl">{{i.service_fee_cycle}}</p>
					<p class="detail-price fr">{{i.fee_price}}</p>
				</dd>
			</dl>
			<!--  -->
			<ul class="discount">
				<li class="ov" @click="uptonList" >
					<div class="fl"> <span class="can-use">{{uptonNumber}}</span></div>
					<div class="fr">{{uptonAmount}} &gt;</div> 
				</li>
				<li class="ov coupon">
					<span class="fl" ></span>
					<span class="fr bigfont">{{reduceAmt}} </span>
				</li>
			</ul>
			
			<!--  -->
			<div class="payCount">
				<p class="fl"></p>
				<p class="fr bigfont"> {{count}}</p>
			</div>
			<!--  -->
			<form class="invoice">
				<div class="form-row">
					:&nbsp;&nbsp;
					 <input  type="radio" id="yes" value="yes" v-model="needInvoice">
  					 <label for="yes"></label>
					 <input type="radio" id="no" value="no" v-model="needInvoice">
  					 <label for="no"></label>
				</div>
				<div v-if="needInvoice == 'yes'" class="form-row">
					:&nbsp;&nbsp;
					 <input  type="radio" id="person" value="person" v-model="invoiceType">
  					 <label for="person"></label>
					 <input type="radio" id="company" value="company" v-model="invoiceType">
  					 <label for="company"></label>
				</div>
				<div class="form-row" v-if="invoiceType == 'company'">
					<mt-field label="" placeholder="..." v-model="invoiceTitle"></mt-field>
				</div>
				<div class="form-row" v-if="invoiceType == 'company'">
					<mt-field label="" placeholder="..." v-model="creditCode"></mt-field>
				</div>
			</form>
			<!--  -->
			<div style="height:1rem;"></div>
			<div class="pay-btn" @click="wechatPay"></div>
		</div>


		<!--  -->
		<div class="upton-list" v-else>
			<!--  -->
			<div class="can-use">
				<p class="fl"></p>
				<p class="fr">{{uptonNumber}}</p>
			</div>
			<!--  -->
			<div class="uptonDetail" v-for=" (item,index) in uptonData" @click="showIcon(index)">
				<i class="icon" :class="{iconShow:item.selected}" ></i>
				<div class="detail-left fl">
					<div class="upton-name">{{item.title}}</div>
					<p class="validity">{{item.leftDayDes}}</p>
					<span class="text"> {{item.useStartDateStr}}{{item.useEndDateStr}}</span>
				</div>
				<div class="detail-right fr">
					<p class="upton-number">{{item.amount}}</p>
					<p class="upton-type"></p>
				</div>
			</div>
			<!--  -->
			<div class="btn" @click="submit"></div>
		</div>

	</div>
</template>
<script>
	let vm;
	import wx from 'weixin-js-sdk';
	export default {
		data(){
			return {
				invoiceTitle:'',//
				creditCode:'',//
				invoiceType:'person',//
				needInvoice:'no',//
				uptonAmount:'',
				upronAmountNumber:0,//// 
				uptonData:[],
				selectUpton:true,//
				routeParams:{
					bills : this.$route.params.bills,//id 
					stmtId:this.$route.params.stmtId,//
					totalPrice:this.$route.params.totalPrice,//
					reduceMode:this.$route.params.reduceMode,//
				},
				count:0,//
				hasReduce:'0',//
				reduceAmt:'0',//
				verNumber:'',//
				addr:'',//
				area:'',//
				feeList:'',//
				uptonNumber:0,//
				couponId:'',//id
				mianBill:'0',//id
				mianAmt:0.00,

			}
		},
		beforeCreate(){//
			
			//alert(1)
		},
		created(){
			vm = this;
			location.href.reload();
			this.directRightUrl();
			if(vm.routeParams.stmtId == " "){
				vm.routeParams.stmtId = ""
			}
			vm.receiveData.wxconfig(vm,wx,['chooseWXPay']);
			//  
			vm.calcReduceAmt()
		},
		watch:{
			needInvoice:function(){
				if(this.needInvoice == 'yes'){
					console.log('yes')
				}else{
					console.log('no')
				}
			}
		},
		mounted(){
			//
			let url = "getBillDetail";
			vm.receiveData.getData(
				vm,
				url,
				'data',
				function(){
	  				let useDate = vm.data.result.fee_data[0];
	  				let mianBill = vm.data.result.mianBill;
	  				let mianAmt = vm.data.result.mianAmt;
	  				if ( mianBill) {
						vm.mianBill = vm.data.result.mianBill;
					}
					if ( mianAmt) {
						vm.mianAmt = vm.data.result.mianAmt;
					}



	  				//
	  				vm.verNumber = useDate.ver_no;
	  				//
	  				vm.addr = useDate.cell_addr;
	  				//
	  				vm.area = useDate.cnst_area;
	  				//
	  				vm.feeList = useDate.fee_name;
	  			},

	  			{
	  				billId :vm.routeParams.bills,
	  				stmtId :vm.routeParams.stmtId
	  			}
	  		);
	  		//   :
	  		 let url2 = 'updateCouponStatus';
	  		vm.receiveData.getData(vm,url2,'temp',function(){
	  			// 
	  			let url3 = 'getCouponsPayWuYe';
	  			vm.receiveData.getData(vm,url3,'uptonData',function(){
	  				vm.uptonData = vm.uptonData.result;
	  				vm.uptonNumber = vm.uptonData.length

	  			})
	  		})

		},
		
		methods:{
			// url
			directRightUrl () {
			  let paths = window.location.href.split('#')
			  paths[1] = paths[1] || '/'
			  // #!
			  if (paths[0].charAt(paths[0].length - 1) !== '?') {
			    paths[0] = `${paths[0]}?`
			  }
			  if (paths[1].charAt(0) === '!') {
			     paths[1] = paths[1].substr(1)
			  }
			  let url = `${paths[0]}#${paths[1]}`
			  if (window.location.href !== url) {
			    window.location.href = url
			  }
			},

			//
			showIcon(index){
				if(vm.uptonData[index].selected){
					vm.$set(vm.uptonData[index],'selected',false);
				}else{
					vm.$set(vm.uptonData[index],'selected',true);
					for(let i in vm.uptonData){
						if(i != index && vm.uptonData[i].selected == true){
							vm.$set(vm.uptonData[i],'selected',false);
							break;							
						}
					}
				}
			},
			//
			calcReduceAmt(){
				//
				vm.count = vm.routeParams.totalPrice;

				let reduced_amt = 0;//
				let reduce_rate = 0;//
				if ("0" == vm.routeParams.reduceMode) {	// 
					return;
				}else if ("1" == vm.routeParams.reduceMode) {	//
					reduce_rate = "1";
					reduced_amt=Math.round(vm.count*reduce_rate)/reduce_rate;
					vm.hasReduce = "1";
				}else if ("2" == vm.routeParams.reduceMode) {	//
					reduce_rate = "10";
					reduced_amt=Math.round(vm.count*reduce_rate)/reduce_rate;
					o.hasReduce = "1";
				}else {
					return;
				}
				vm.count = reduced_amt.toFixed(2);//
				vm.reduceAmt = parseFloat(vm.routeParams.totalPrice) - parseFloat(vm.count);
				vm.reduceAmt = vm.reduceAmt.toFixed(2);//
			},
			//
			uptonList(){
				if(vm.uptonNumber == 0){//
					return
				}else{
					vm.selectUpton = false;	
				}
				
			},
			submit(){//
				let flag = false;
				for(let i in vm.uptonData){
					if(vm.uptonData[i].selected == true){
						flag = true;
						vm.couponId = vm.uptonData[i].id;//id
						vm.uptonAmount = '' + vm.uptonData[i].amount;
						vm.upronAmountNumber = vm.uptonData[i].amount;
						vm.count -= vm.uptonData[i].amount
						vm.count = vm.count.toFixed(2);//
						if(vm.count < 0 ){
							vm.count = 0;
						}
					}
					if(!flag){
						vm.uptonAmount = "";
						vm.upronAmountNumber = 0,
						vm.couponId = '';
						vm.calcReduceAmt();
					}
					vm.selectUpton = true;
				}
			},
			// postpost
			wechatPay(){
				//vm, url, params,backdataname,callback
				//let url = 'getPrePayInfo'
				let url = "getPrePayInfo?billId="+vm.routeParams.bills+"&stmtId="+vm.routeParams.stmtId+"&couponUnit="+vm.upronAmountNumber+
        	"&couponNum=1&couponId="+vm.couponId+"&mianBill="+vm.mianBill+"&mianAmt="+vm.mianAmt+"&reduceAmt="+vm.reduceAmt+"&invoice_title_type="+''+"&credit_code="+''+"&invoice_title=";
				vm.receiveData.postData(
					vm,
					url,
					{},
					// {
					// 	billId:vm.routeParams.bills,//id
					// 	stmtId:vm.routeParams.stmtId,//
					// 	couponUnit:vm.upronAmountNumber,//
					// 	couponNum:1,//
					// 	couponId:vm.couponId,//id
					// 	mianBill:vm.mianBill,//id
					// 	mianAmt :vm.mianAmt,
					// 	reduceAmt :vm.reduceAmt,
					// 	invoice_title_type:'',
					// 	credit_code:'',
					// 	invoice_title:'',
					// },
					'postResultData',
					function(){
						let wd = vm.postResultData//
						console.log(wd)
						//
						wx.config({
		                    debug: true, // ,apialertpclogpc
		                    appId: wd.appid, // 
		                    timestamp: wd.timestamp, // 
		                    nonceStr: wd.nonceStr, // 
		                    signature: wd.signature,// 1
		                    jsApiList: ['chooseWXPay'] // JSJS2
		                });
		                //
						 wx.chooseWXPay({
			                "timestamp":wd.timestamp,
			                "nonceStr":wd.noncestr,
			                "package":wd.packageValue,
			                "signType":wd.signtype,
			                "paySign":wd.paysign,
							"appId":wd.appid,
			          	    success: function (res) {
			                	alert('')
			                	//notifyPaySuccess();
			          	    },
			          	    fail:function(res) {
			          	    	alert('')
			          	 //    	alert(JSON.stringify(res));
						        	// o.isPaying = false;
						        	// commonui.hideAjaxLoading();
						        	// $("#zzmb").hide();
			          	    },
			          	    cancel:function(res){
			          	    	alert('')
								// console.log(JSON.stringify(n));
								// o.isPaying = false;
						  //       commonui.hideAjaxLoading();
						  //       $("#zzmb").hide();
							}
			          	    
			          	});
					}
				)
			}

		}


	}
</script>


